body{
    background-color: hsl(200, 100%, 90%);
    color: coral;
    padding: 1rem;
    margin: 0 auto;
}

.items-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}

hr{
    color: coral;
}

.btn {
    background: hsl(200, 100%, 50%, 0.1);
    border: 1px solid hsl(200, 100%, 50%);
    color: hsl(200, 100%, 50%);
    padding: 0.25em 0.5em;
    border-radius: 0.25em;
    cursor: pointer;
    outline: none;
}


